import React, {useState, useEffect} from "react";
import axios from 'axios';
import {Redirect, Link} from 'react-router-dom';


axios.defaults.baseURL = 'http://localhost:8080';

export function RegisterContainer() {

    let [isLogin, loginStatus] = useState(false);
    let [user, setUser] = useState('');
    let [passwd, setPasswd] = useState('');
    let [userName, setName] = useState('')
    let [userSession, setSession] = useState('')
    let [datosCorrectos, setBadLogin] = useState(false)
    let [errorInfo, setErrorInfo] = useState('')
    useEffect(() => {
        status();

    },[]);

    async function register(){
        
       setBadLogin(false)
       if (user === "" || passwd === ""){
            setBadLogin(true)
            return false
       }
        
        axios.post(`/register/?username=${user}&password=${passwd}`, {withCredentials: true})
        .then(res => {
            console.log(res);
            if (res.status === 500){
                setErrorInfo("El usuario que quiere registrar ya existe")
            } else {
                setName(res.data)
                setSession(res.idSession)
                setBadLogin(false)
                loginStatus(true)
                
            }
            
        })
        .catch(err => {
            setBadLogin(true)
        })


        return false
    }
    
    async function status(){
        
        axios.get('/status', {withCredentials: true})
        .then(res => {
            loginStatus(true)
            setSession(res.data.idSession)
        })
        .catch(err => {
            loginStatus(false)
        })

        return false
    }

    function updateUser(event){
        setUser(event.target.value)
    }

    function updatePasswd(event){
        setPasswd(event.target.value)
    }

    return (
        <React.Fragment>
            {isLogin ? <Redirect to={{pathname: "/", state: {session: userSession, name: userName} }}/> : <div></div>}
            <React.Fragment>
                <form className="m-4 p-4 bg-secondary">
                    <div className="mb-4"><h1>Registracion</h1></div>
                    <fieldset>
                        <div className="form-group">
                            <label>Usuario</label>
                            <input type="email" className="form-control" id="registerUser" aria-describedby="emailHelp" placeholder="ingresa usuario" onChange={updateUser.bind(this)}/>
                        </div>
                        <div className="form-group">
                            <label>Password</label>
                            <input type="password" className="form-control" id="registerPassword" placeholder="Password" onChange={updatePasswd.bind(this)} />
                        </div>
                    </fieldset>
                    <button onClick={e => {e.preventDefault(); register()}} className="btn btn-primary">Crear usuario</button>
                </form>
                
            </React.Fragment>
            <React.Fragment>
                {!datosCorrectos ?
                <div></div>
                :
                <div style={{color: "red"}}>Hay algun problema con los datos ingresados </div>
                }
            </React.Fragment>
            
        </React.Fragment>
    );
 
}
