import React, {useState, useEffect} from 'react'
import {useLocation} from 'react-router-dom'
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";


function useQuery() {
  return new URLSearchParams(useLocation().search);
}

export function RandomContainer() {
    let query = useQuery();
        
    let [numbers, setNumbers] = useState([]);
    
    useEffect(()=>{
      
      let cant;
      if(query.get('cant')){
        cant = query.get('cant')
      } else {
        cant = ''
      }

      fetch(`http://localhost:8080/randoms/${cant}`)
      .then(res => res.json())
      .then((data) => {
        console.log(data);
        setNumbers(data)
      })
      .catch(console.log)
    },[])

    return (
      <React.Fragment>
        { numbers && numbers.length ? 
            <React.Fragment>
              <h1>Numeros Random</h1>
              <table>
                      <thead>
                          <tr>
                            <th>Numero</th>
                            <th>Cantidad de apariciones</th>
                          </tr>
                      </thead>
                      <tbody>
                        {numbers.map((i)=><tr key={i[0]}>
                            <td>{i[0]}</td>
                            <td>{i[1]}</td>
                        </tr>
                        )}

                      </tbody>
                  </table>
              
            </React.Fragment>
          :
          <div className="mt-5">
                <div>
                    <h1>Cargando Numeros Random</h1>
                </div>
                <Loader type="Puff" color="#00BFFF" height={100} width={100} timeout={300000}/> 
          </div>
        }   
      </React.Fragment>
    );
}