import React, {useState, useEffect} from 'react'
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";



export function InfoContainer() {
        
    let [systemInfo, setInfo] = useState([]);


    useEffect(()=>{
      fetch('http://localhost:8080/info')
      .then(res => res.json())
      .then((data) => {
        setInfo(data)
      })
      .catch(console.log)
    },[])

    return (
      <React.Fragment>
        { systemInfo && systemInfo.args ? 
            <React.Fragment>
              <h1>Informacion del sistema</h1>
              <div className="row justify-content-center">
              <table className="table">
                <thead>
                    <tr>
                    <th scope="col"></th>
                    <th scope="col">Informacion</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                    <td>Argumentos</td>
                    <td>
                        <p>{systemInfo.args[0]}</p>
                        <p>{systemInfo.args[1]}</p>
                        <p>{systemInfo.args[2]}</p>
                        <p>{systemInfo.args[3]}</p>
                    </td>
                    </tr>
                    <tr>
                        <td>Plataforma</td>
                        <td>{systemInfo.plataform}</td>
                    </tr>
                    <tr>
                        <td>Version de Node</td>
                        <td>{systemInfo.nodeversion}</td>
                    </tr>
                    <tr>
                        <td>Uso de memoria</td>
                        <td>
                            <p>External: {systemInfo.memory.external}</p>
                            <p>Heap Total: {systemInfo.memory.heapTotal}</p>
                            <p>Heap Used: {systemInfo.memory.heapUsed}</p>

                        </td>
                    </tr>
                    <tr>
                        <td>Path de ejecución</td>
                        <td>{systemInfo.args[3]}</td>
                    </tr>
                    <tr>
                        <td>Process id</td>
                        <td>{systemInfo.process}</td>
                    </tr>
                    <tr>
                        <td>Carpeta corriente</td>
                        <td>{systemInfo.execPath}</td>
                    </tr>
                </tbody>
                </table>
              </div>
            </React.Fragment>
          :
          <div className="mt-5">
                <div>
                    <h1>Cargando informacion del sistema</h1>
                </div>
                <Loader type="Puff" color="#00BFFF" height={100} width={100} timeout={3000000}/> 
          </div>
        }   
      </React.Fragment>
    );
}
