import React, {useState, useEffect} from "react";
import axios from 'axios';
import {Redirect, Link} from 'react-router-dom';

axios.defaults.baseURL = 'http://localhost:8080';

export function LoginContainer() {

    let [isLogin, loginStatus] = useState(false);
    let [user, setUser] = useState('');
    let [passwd, setPasswd] = useState('');
    let [userName, setName] = useState('')
    let [userSession, setSession] = useState('')
    let [datosCorrectos, setBadLogin] = useState(false)



    async function login(){
        
       setBadLogin(false)
        if (user === "" || passwd === "") {
            setBadLogin(true)
            return false
        }

        axios.post(`/login/?username=${user}&password=${passwd}`, {withCredentials: true})
        .then(res => {
            console.log(res);
            setName(res.data)
            setBadLogin(false)
            setUser('')
            setPasswd('')
            loginStatus(true)
        })
        .catch(err => {
            console.log(err);
            setBadLogin(true)
        })


        return false
    }


    async function status(){
        
        axios.get('/status', {withCredentials: true})
        .then(res => {
            setSession(res.data.idSession)
        })
        .catch(err => {
            loginStatus(false)
        })

        return false
    }

    
    function updateUser(event){
        setUser(event.target.value)
    }

    function updatePasswd(event){
        setPasswd(event.target.value)
    }

    return (
        <React.Fragment>
            {isLogin ? <Redirect to={{pathname: "/", state: {session: userSession, name: userName} }}/> : <div></div>}
            <React.Fragment>
                <form className="m-4 p-4 bg-primary" onSubmit={()=>login()}>
                <div className="mb-4"><h1>Login</h1></div>
                    <fieldset>
                        <div className="form-group">
                            <label>Usuario</label>
                            <input type="email" className="form-control" id="loginUser" aria-describedby="emailHelp" placeholder="ingresa usuario" onChange={updateUser.bind(this)}/>
                        </div>
                        <div className="form-group">
                            <label>Password</label>
                            <input type="password" className="form-control" id="loginPassword" placeholder="Password" onChange={updatePasswd.bind(this)} />
                        </div>
                    </fieldset>
                    <button onClick={e => {e.preventDefault(); login()}} className="btn btn-secondary">Ingresar</button>
                    <div className="bg-secondary"><a href="http://localhost:8080/auth/facebook">LOGIN FACEBOOK</a></div>
                    <div className="fb-login-button" data-width="" data-size="large" data-button-type="login_with" data-layout="default" data-auto-logout-link="false" data-use-continue-as="false"></div>
                </form>
                <div className="mt-3">
                    <p>¿No tenes cuenta?</p>
                    <Link to="/register"><button className="btn btn-primary">Registrarse</button></Link>
                    
                </div>
            </React.Fragment>
            <React.Fragment>
                {!datosCorrectos ?
                <div></div>
                :
                <div style={{color: "red"}}>Datos Incorrectos</div>
                }
            </React.Fragment>
            
        </React.Fragment>
    );
 
}
