import React, {useState, useEffect} from "react";
import axios from 'axios';
import { ItemListContainer } from "../ItemListContainer/ItemListContainer"


axios.defaults.baseURL = 'http://localhost:8080';


export function ProductContainer(props){
    
    
    let [isLogin, loginStatus] = useState(false);
    let [user, setUser] = useState('');


    useEffect(() => {
        axios.get('http://localhost:8080/auth/facebook', { withCredentials: true })
        .then(res => {
            console.log(res);
            
        })
        .catch(err => console.log(err))

        // if(props.location.state !== undefined){
        //     setUser(props.location.state.name)
        //     loginStatus(true);
        // }
        // console.log(props.match.params);

    },[]);
    
    async function logout(){
        axios.get('/logout', {withCredentials: true})
        .then(res => {
            loginStatus(false)
            localStorage.clear("token");
            props.history.push("/")
            
            
        })
        .catch(console.log)
    }

    return (
        <React.Fragment>     
            {isLogin ?
                <React.Fragment>
                    <div className="row" style={{backgroundColor: "#d3d3d3"}}>
                        <h1 className="m-3">Bienvenido {user}</h1>
                        <button onClick={()=>logout()}className="btn btn-warning">Desloguerse</button>
                    </div>
                    <ItemListContainer />
                </React.Fragment>
            :
                <React.Fragment>     
                    <div className="mt-4">
                        <p>No se encuentra logueado, por favor inicie sesion para ver los productos</p>
                        <a href="/login"><button className="btn btn-primary">Iniciar Sesion</button></a>
                    </div>
                </React.Fragment>
            }
            </React.Fragment>
    )
}